var NgAnnotations = {};

// components
NgAnnotations.controller = require('src/decorators/components/controller').default;
NgAnnotations.component = require('src/decorators/components/component').default;
NgAnnotations.service = require('src/decorators/components/service').default;
NgAnnotations.animation = require('src/decorators/components/animation').default;
NgAnnotations.config = require('src/decorators/components/config').default;
NgAnnotations.directive = require('src/decorators/components/directive').default;
NgAnnotations.factory = require('src/decorators/components/factory').default;
NgAnnotations.filter = require('src/decorators/components/filter').default;
NgAnnotations.provider = require('src/decorators/components/provider').default;
NgAnnotations.run = require('src/decorators/components/run').default;
NgAnnotations.decorator = require('src/decorators/components/decorator').default;

// wrappers
NgAnnotations.constant = require('src/wrappers/constant').default;
NgAnnotations.value = require('src/wrappers/value').default;

// utils
NgAnnotations.inject = require('src/decorators/utils/inject').inject;
NgAnnotations.autobind = require('src/decorators/utils/autobind').default;
NgAnnotations.attach = require('src/decorators/utils/attach').default;
NgAnnotations.conceal = require('src/decorators/utils/conceal').default;

export default window.ngAnnotations = NgAnnotations;


