/**
 * @decorator: @autobind
 * @type: statement
 *
 * bind a method to its current context
 *
 */
export default function autobind (props, name, descriptor) {

  console.log('descriptor', descriptor)
  console.log('name', name)
  console.log('props', props)
  // console.log('descriptor.get()', descriptor.get())
  let fn = descriptor.value;

  console.log('descriptor.value', descriptor.value)

	if (typeof fn !== 'function') {
		throw Error(`@autobind decorator can only be applied to methods not: ${typeof fn}`);
	}
  return {
    configurable : true,
    get          : function get () {
      var boundFn = fn.bind(this);
      Object.defineProperty(this, name, {
        value        : boundFn,
        configurable : true,
        writable     : true
      });
      return boundFn;
    }
  };
}


